.AutocompleteSelect {
  display: inline-flex;
  flex-flow: column;
  justify-content: center;
  position: relative;
  min-width: 250px;
  min-height: 36px;
  user-select: none;
}
@keyframes clockwise {
  0% {
    -webkit-transform: rotate(-90deg);
  }
  100% {
    -webkit-transform: translateX(0px);
  }
}
@keyframes anticlockwise {
  0% {
    -webkit-transform: rotate(90deg);
  }
  100% {
    -webkit-transform: translateX(0px);
  }
}
.AutocompleteSelect > .input {
  width: 100%;
  position: relative;
}
.AutocompleteSelect .Scrollable {
  padding-right: 0;
  min-width: 100%;
}
.AutocompleteSelect .selected.full .DummyChild {
  padding-left: 35px;
  padding-right: 10px;
}
.AutocompleteSelect.inline-multiple > .label {
  top: -12px;
}
.AutocompleteSelect.inline-multiple > .selected {
  flex-flow: row wrap;
}
.AutocompleteSelect.multiple-mini-inline {
  height: 42px;
}
.AutocompleteSelect.multiple-mini-inline > .label {
  cursor: pointer;
  width: 100%;
  margin-left: -10px;
  padding: 10px;
  box-sizing: border-box;
  top: 0;
}
.AutocompleteSelect.multiple-mini-inline > .label.active {
  padding: 0 10px;
}
.AutocompleteSelect.multiple-mini-inline > .selected {
  width: 250px;
}
.AutocompleteSelect.multiple-mini-inline > .input {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 38px;
  z-index: 2;
  background-color: white;
  min-width: 130%;
}
.AutocompleteSelect.multiple-mini-inline > .input .TextField {
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  box-sizing: border-box;
}
.AutocompleteSelect.multiple-mini-inline > .input.hidden {
  opacity: 0;
  visibility: hidden;
}
.AutocompleteSelect.multiple-mini-inline .FilteredList .child:first-child {
  margin-top: 0;
}
.AutocompleteSelect .TextField {
  margin-top: 8px;
}
.AutocompleteSelect .TextField input {
  font-size: 14px;
}
.AutocompleteSelect > .label {
  position: absolute;
  transition: all 0.2s ease;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  cursor: text;
  user-select: none;
  left: 4px;
  top: 12px;
}
.AutocompleteSelect > .label.active {
  font-size: 10px;
  top: 2px;
}
.AutocompleteSelect > .label.active .arrow {
  border-width: 4px 4px 0 4px;
  margin-bottom: 1px;
}
.AutocompleteSelect > .label .arrow {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: rgba(0, 0, 0, 0.5) transparent transparent transparent;
  display: inline-block;
  margin-left: 5px;
  margin-bottom: 2px;
  transition: all 0.2s ease;
}
.AutocompleteSelect > .selected {
  margin-top: 8px;
  display: flex;
  cursor: pointer;
}
.AutocompleteSelect > .selected > .reset {
  min-width: 36px;
  max-width: 36px;
  position: absolute;
  right: 0;
  top: 0;
  background-color: white;
}
.AutocompleteSelect > .selected > .reset .material-icons {
  margin: 0;
  color: #BFBFBF;
  font-size: 20px;
}
.AutocompleteSelect > .selected.nolabel {
  margin-top: 0px;
  border-bottom: 1px solid #e5e3e3;
}
.AutocompleteSelect > .selected.nolabel .DummyChild {
  display: inline-flex;
  align-items: center;
  flex-grow: 1;
  font-size: 16px;
  margin: 0px 10px;
  margin-bottom: 5px;
  height: 19px;
}
.AutocompleteSelect > .selected.nolabel .arrow {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: rgba(0, 0, 0, 0.5) transparent transparent transparent;
  display: inline-block;
  transition: all 0.2s ease;
  margin: 0px 17px 3px 5px;
}
.AutocompleteSelect > .selected.inline {
  font-size: 14px;
  font-family: Roboto, sans-serif;
  color: #1e252d;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.AutocompleteSelect > .selected.clickable {
  overflow: hidden;
  cursor: pointer;
  padding: 10px;
  margin-left: -10px;
  margin-right: -10px;
}
.AutocompleteSelect > .selected > .value {
  cursor: pointer;
  margin: 5px 10px 5px 0px;
  display: inline-flex;
  flex-flow: row wrap;
  height: 30px;
  align-items: center;
  background-color: #f0f0f0;
  border-radius: 30px;
  position: relative;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  color: #1e252d;
}
.AutocompleteSelect > .selected > .value .button {
  cursor: pointer;
  min-width: 30px;
  width: 30px;
  height: 30px;
  opacity: 0;
  position: absolute;
  top: 0px;
  left: 0px;
  transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  background-color: deepskyblue;
  color: white;
  border-radius: 30px;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  animation: anticlockwise 0.15s linear;
}
.AutocompleteSelect > .selected > .value .button .material-icons {
  font-size: 22px;
}
.AutocompleteSelect > .selected > .value:hover .button {
  width: 30px;
  height: 30px;
  opacity: 1;
  animation: clockwise 0.15s linear;
  overflow: hidden;
  position: absolute;
}
