.Switch {
  display: inline-flex;
  padding-top: 5px;
}
.Switch .switch-input {
  display: none;
}
.Switch .switch-label {
  position: relative;
  display: inline-flex;
  align-items: center;
  min-width: 38px;
  height: 24px;
  cursor: pointer;
  font-weight: 500;
  text-align: left;
  user-select: none;
  margin: 0;
  padding: 0;
  padding-left: 44px;
  box-sizing: border-box;
}
.Switch .switch-label:before, .Switch .switch-label:after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  margin: 0;
  outline: 0;
  top: 50%;
  transform: translate(0, -50%);
  transition: all 0.3s ease;
}
.Switch .switch-label:before {
  left: 1px;
  width: 34px;
  height: 14px;
  background-color: #9E9E9E;
  border-radius: 8px;
}
.Switch .switch-label:after {
  left: 0;
  width: 20px;
  height: 20px;
  background-color: #FAFAFA;
  border-radius: 50%;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.14), 0 2px 2px 0 rgba(0, 0, 0, 0.098), 0 1px 5px 0 rgba(0, 0, 0, 0.084);
}
.Switch .switch-label .toggle--on {
  display: none;
}
.Switch .switch-label .toggle--off {
  display: inline-block;
}
.Switch .switch-label .label {
  margin-right: 5px;
}
.Switch .switch-input:checked + .switch-label:before {
  background-color: #A5D6A7;
}
.Switch .switch-input:checked + .switch-label:after {
  background-color: #4CAF50;
  transform: translate(80%, -50%);
}
.Switch .switch-input:checked + .switch-label .toggle--on {
  display: inline-block;
}
.Switch .switch-input:checked + .switch-label .toggle--off {
  display: none;
}
