.Button {
  position: relative;
  min-width: 64px;
  min-height: 36px;
  height: 24px;
  margin: 0px;
  padding: 0px 16px;
  border: none;
  outline: none;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1.25px;
  background: #dfdfdf;
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  user-select: none;
  border-radius: 3px;
  transition: all 0.2s ease;
}
.Button.flat {
  background-color: transparent;
}
.Button.flat:hover {
  background-color: rgba(0, 0, 0, 0.05);
}
.Button.green {
  background-color: #4AC263;
  color: white;
}
.Button.green:hover {
  background-color: #3db556;
}
.Button.blue {
  background-color: #61a0b2;
  color: white;
}
.Button.blue:hover {
  background-color: #5295a8;
}
.Button.red {
  background-color: #F44336;
  color: white;
}
.Button.red:hover {
  background-color: #f32c1e;
}
.Button.black {
  background-color: #1e252d;
  color: white;
}
.Button.black:hover {
  background-color: #14181e;
}
.Button.gray {
  background: #D9D9D9;
  color: #999999;
}
.Button.gray:hover {
  background-color: #cccccc;
}
.Button.small {
  padding: 0px 8px;
  min-height: 16px;
  min-width: 16px;
}
.Button > .material-icons {
  margin-right: 10px;
}
.Button.round {
  padding: 0px;
  min-width: 36px;
  border-radius: 50%;
}
.Button.round > .material-icons {
  margin: 0px;
}
.Button .ripple {
  position: absolute;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 100%;
  transform: scale(0);
  pointer-events: none;
  transition: opacity 0.2s ease-out;
}
.Button .ripple.show {
  animation: ripple 0.35s ease-out;
}
.Button:active .ripple.showed {
  opacity: 1;
}
.Button .ripple.showed {
  transform: scale(2);
  opacity: 0;
}
@keyframes ripple {
  to {
    transform: scale(2);
  }
}
.Button.raised {
  transition: color 0.2s ease-out, background-color 0.2s ease-out, box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
}
.Button.raised:hover {
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.25);
}
.Button.raised:active {
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.35);
}
.Button.disabled {
  box-shadow: none;
  background: #D9D9D9;
  color: #999999;
}
.Button.disabled:hover {
  background-color: #cccccc;
}
.Button::-moz-focus-inner {
  border: 0;
}
