.DNDContainer .DroppableList .Draggable {
  transform: unset;
  transition: unset;
  opacity: 1;
}
.DNDContainer .DroppableList .Draggable.dragged {
  transition: unset !important;
}
.DNDContainer .DroppableList.hover .Draggable {
  transition: transform 0.2s ease-in;
}
.DNDContainer .DroppableList.resetting .Draggable {
  transition: transform 0.1s ease-in;
}
