.Snackbar {
  transform: translateX(-50%);
  overflow: hidden;
  background-color: #323232;
  padding: 14px 24px 14px 24px;
  color: #fff;
  min-width: 288px;
  max-width: 568px;
  position: fixed;
  bottom: -50px;
  left: 50%;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  opacity: 0;
  transition: bottom 0.25s ease, opacity 0.2s ease;
  border-radius: 4px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.Snackbar.shown {
  bottom: 15px;
  opacity: 1;
}
.Snackbar .message {
  overflow: hidden;
  line-height: 1.2em;
  min-height: 1.2em;
  max-height: 2.4em;
  flex: 1;
}
.Snackbar .action {
  text-transform: uppercase;
  font-weight: bold;
  margin-left: 48px;
  color: #bf3727;
}
.Snackbar .action a {
  text-decoration: none;
  color: #bf3727;
}
