.Modal {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 100%;
  overflow-y: scroll;
  transition: bottom 0ms linear;
  transition-delay: 0.2s;
}
.Modal.show {
  transition-delay: 0s;
  bottom: 0;
}
.Modal > .overlay {
  position: fixed;
  top: 0;
  bottom: 100%;
  left: 0;
  right: 0;
  transition: opacity 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  background-color: black;
  pointer-events: none;
}
.Modal > .overlay.show {
  opacity: 0.5;
  bottom: 0;
}
.Modal > .content {
  left: 50%;
  top: 10vh;
  margin-bottom: 10vh;
  font-size: 16px;
  padding: 0;
  opacity: 0;
  transition: opacity 0.25s ease, transform 0.25s ease;
  transform: scaleY(0) translateY(-200px) translateX(-50%);
  transform-origin: top;
  position: absolute;
  border-radius: 2px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
}
.Modal > .content.show {
  opacity: 1;
  transform: scaleY(1) translateY(0px) translateX(-50%);
}
.Modal > .content > .Scrollable {
  overflow: visible;
  max-height: 80vh;
}
.Modal > .content > .Scrollable > .viewport {
  min-width: 10px;
  min-height: 10px;
}
.Modal > .content > .Button.close {
  top: 10px;
  right: 10px;
  position: absolute;
}
